import classes from './Services.module.scss';
import { NavLink } from "react-router-dom";
import Search from "../../assets/icons/search.svg";
import Worker from "../../assets/icons/worker.svg";
import Chart from "../../assets/icons/chart.svg";
import Purse from "../../assets/icons/purse.svg";
import More from "../../assets/icons/more.svg";
import { Slide } from "react-awesome-reveal";
import { FC } from "react";

const Services: FC = (): JSX.Element => (
    <Slide triggerOnce>
        <nav className={classes.wrapper}>
            <NavLink to={'/about'} className={classes.item}>
                <img className={classes.icon} src={ Search } alt={''} />
                <span>      </span>
            </NavLink>
            <NavLink to={'/about'} className={classes.item}>
                <img className={classes.icon} src={ Worker } alt={''} />
                <span>    </span>
            </NavLink>
            <NavLink to={'/about'} className={classes.item}>
                <img className={classes.icon} src={ Chart } alt={''} />
                <span>      </span>
            </NavLink>
            <NavLink to={'/about'} className={classes.item}>
                <img className={classes.icon} src={ Purse } alt={''} />
                <span>    </span>
            </NavLink>
            <NavLink to={'/about'} className={`${classes.item} ${classes.last}`}>
                <img className={classes.icon} src={ More } alt={''} />
                <span> ...</span>
            </NavLink>
        </nav>
    </Slide>
);
export default Services;