import classes from './Information.module.scss';
import { useSelector } from 'react-redux';
import { FC } from "react";
import { RootState } from "../../store/store";

const Information: FC = (): JSX.Element => {
    const information = useSelector((state: RootState) => state.aboutPage.information);

    return (
        <>
            <article className={classes.block}>
                <p className={classes.text}> : <span>{ information.legalAddress }</span></p>
                <p className={classes.text}>: <span>{ information.supervisor }</span></p>
                <p className={classes.text}>: <a href={`tel:${ information.accountingPhone }`}>{ information.accountingPhone }</a></p>
                <p className={classes.text}> : <a href={`tel:${ information.employmentDepartmentPhone }`}>{ information.employmentDepartmentPhone }</a></p>
            </article>
            <p className={classes.text}>  : <a href={`tel:${ information.hotlinePhone }`}>{ information.hotlinePhone }</a></p>
        </>
    );
};
export default Information;