import classes from './Questionnaires.module.scss';
import { Link } from 'react-router-dom';
import Button from "@mui/material/Button";
import Wrapper from "../../components/CommonComponents/Wrapper/Wrapper";
import ForCitizensFile from '../../assets/documents/questionnaires/for-citizens.doc';
import ForEmployersFile from '../../assets/documents/questionnaires/for-employers.doc';
import { FC } from "react";

const Questionnaires: FC = (): JSX.Element => (
    <Wrapper title={'    '}>
        <div className={classes.information}>
            <p className={`${classes.text} ${classes.bold}`}>       .</p>
            <p className={`${classes.text} ${classes.bold}`}>            ,      .</p>
            <p className={classes.text}>        ,                <a className={classes.link} href={'mailto:krt@szn74.ru'}>krt@szn74.ru</a>.</p>
        </div>
        <ul className={classes.list}>
            <li className={classes.item}>
                <Link to={ForCitizensFile} target={'_blank'} download={' '}>
                    <Button className={classes.button} variant="contained"> </Button>
                </Link>
            </li>
            <li className={classes.item}>
                <Link to={ForEmployersFile} target={'_blank'} download={' '}>
                    <Button className={classes.button} variant="contained"> </Button>
                </Link>
            </li>
        </ul>
        <div className={classes.information}>
            <p className={`${classes.text} ${classes.bold}`}>               .</p>
            <p className={`${classes.text} ${classes.italic}`}> : ~ 5-10 .</p>
            <p className={classes.text}>   !</p>
        </div>
    </Wrapper>
);
export default Questionnaires;